import React, { Component } from 'react';
import PropTypes from 'prop-types';
import CopyToClipboard from 'react-copy-to-clipboard';
import prettyBytes from 'pretty-bytes';
import classnames from 'classnames';
import uuidv4 from 'uuid/v4';
import {
  Link,
} from 'react-router-dom';
import QRCode from 'qrcode.react';
import Dropzone from 'react-dropzone';

import Icon from './common/Icon';
import Button from './common/Button';
import Steps from './common/Steps';
import Toast from './common/Toast';
import FileBox from './FileBox';
import Peer from '../Peer';
import FileChunker from '../FileChunker';
import { calcPercent } from '../common/util';
import { prepareSend, deleteRecvCode } from '../actions/file';

import styles from './SendFilePanel.cm.styl';

const Step = Steps.Step;

class SendFilePanel extends Component {
  constructor(props) {
    super(props);
    this.peer = new Peer();
    this.inputRef = React.createRef();
    this.onChangeFile = this.onChangeFile.bind(this);
    this.onRemoveFile = this.onRemoveFile.bind(this);
    this.onClickSelectDone = this.onClickSelectDone.bind(this);
    this.onClickBack = this.onClickBack.bind(this);
    this.onReset = this.onReset.bind(this); // 

    this.sendSizes = {};
    this.bps = 0;

    this.timer = setInterval(() => {
      const files = this.props.files.map(f => {
        const sendSize = this.sendSizes[f.uid] || 0;
        const pct = calcPercent(sendSize, f.size);
        return {
          ...f,
          pct,
        };
      });

      this.props.setState({ files, bps: this.bps });
      this.bps = 0;
    }, 1000);
  }

  componentDidMount() {
    document.title = 'P2P - ';
  }

  componentWillUnmount() {
    clearInterval(this.timer);
  }

  onClickSelectDone() {
    this.props.setState({
      curStep: 2,
    });

    const files = this.props.files.map(f => {
      return {
        uid: f.uid,
        name: f.name,
        size: f.size,
        type: f.type,
      };
    });
    prepareSend(files);

    const peer = this.peer;

    peer.on('connecting', () => {
      this.props.setState({
        peerState: 'connecting',
      });
    });

    peer.on('connected', () => {
      this.props.setState({
        peerState: 'connected',
      });
    });

    peer.on('connectFailed', () => {
      this.props.setState({
        peerState: 'connectFailed',
      });
      Toast.error('');
    });

    peer.on('disconnected', async() => {
      this.props.setState({
        peerState: 'disconnected',
      });
      Toast.error('');
    });

    peer.on('channelOpen', async() => {
      this.props.setState({
        curStep: 3,
        peerState: 'transfer',
      });

      const files = this.props.files;
      for (let i = 0; i < files.length; i++) {
        const file = files[i];
        const fileId = file.uid;
        await peer.sendJSON({
          type: 'fileStart',
          fileId,
        });

        this.props.setState({
          curFileId: fileId,
        });

        const chunker = new FileChunker(file.realFile);
        let done = false;
        let lastOffset = 0;
        while (!done) {
          const result = await chunker.getNextChunk();
          done = result.done;
          const {
            chunk,
            offset,
          } = result;
          try {
            await peer.send(chunk);
          } catch (err) {
            Toast.error('' + err);
            break;
          }
          this.sendSizes[fileId] = offset;
          this.bps += (offset - lastOffset);
          lastOffset = offset;
        }
        if (done) {
          await peer.sendJSON({
            type: 'fileEnd',
            fileId,
          });
        }
      }
    });
  }

  onClickBack() {
    deleteRecvCode(this.props.recvCode);
    this.props.setState({ curStep: this.props.curStep - 1 });
  }

  onReset() {
    this.peer.destroy();
    this.props.setState({
      curStep: 1,
      files: [],
      peerState: '',
    });
  }

  onChangeFile(files) {
    const filteredFiles = files.filter(f => {
      const existed = this.props.files.find(_f1 => {
        const f1 = _f1.realFile;
        if (f1.name === f.name && f1.size === f.size && f1.lastModified === f.lastModified && f1.type === f.type) {
          return true;
        }
        return false;
      });
      return !existed;
    }).map(f => {
      return {
        realFile: f,
        uid: uuidv4(),
        name: f.name,
        size: f.size,
        type: f.type,
      };
    });

    if (filteredFiles.length !== files.length) {
      Toast.info('');
    }

    const nextFiles = this.props.files.concat(filteredFiles);
    this.props.setState({
      files: nextFiles,
    });
    event.target.value = null;
  }

  onRemoveFile(uid) {
    return () => {
      const nextFiles = this.props.files.filter(f => f.uid !== uid);
      this.props.setState({ files: nextFiles });
    };
  }

  renderStep1() {
    const {
      files,
    } = this.props;

    const totalBytes = files.reduce((sum, cur) => {
      return sum + cur.size;
    }, 0);

    return (
      <>
        <Dropzone onDrop={this.onChangeFile} noClick>
          {({ getRootProps, getInputProps, isDragAccept, open }) => (
            <div className={classnames(styles.uploadArea, isDragAccept && styles.dragActive)} {...getRootProps()}>
              {files.length === 0 && (
                <>
                  <div className={styles.iconPlusWrapper}>
                    <Icon name="plus" className={styles.iconPlus} />
                  </div>
                  <div className={styles.uploadTip}>
                    
                  </div>
                  <Button
                    type="primary"
                    className={styles.uploadBtn}
                    onClick={() => open()}
                  >
                    
                  </Button>
                </>
              )}
              {files.length > 0 && (
                <FileBox
                  files={files}
                  removable
                  onRemoveFile={this.onRemoveFile}
                />
              )}
              <input {...getInputProps()}/>
            </div>
          )}
        </Dropzone>
        {files.length === 0 && (
          <div className={styles.recvGuide}>
            
            <Link to="/recv" className={styles.link}> </Link>
          </div>
        )}
        {files.length > 0 && (
          <>
            <div className={styles.uploadMore}>
              <div className={styles.addMore}>
                <span></span>
              </div>
              <div className={styles.fileSummary}>{files.length}  {prettyBytes(totalBytes)}</div>
            </div>
            <Button type="primary" className={styles.btnSelectFileDone} onClick={this.onClickSelectDone}>
              
            </Button>
          </>
        )}
      </>
    );
  }

  renderStep2() {
    const {
      recvCode,
    } = this.props;

    const recvLink = `http://${document.location.host}/recv/${recvCode}`;

    return (
      <>
        <div className={styles.selectSendMethod}>
          
        </div>
        <div className={styles.sendMethod1}>
          1. 
        </div>
        <div className={styles.recvLinkContainer}>
          <span className={styles.recvLink}>
            {recvLink}
          </span>
          <CopyToClipboard text={recvLink} onCopy={() => Toast.success('')}>
            <span className={styles.btnCopy}>
              
            </span>
          </CopyToClipboard>
        </div>
        <div>
          2. 6
        </div>
        <div className={styles.recvCodeContainer}>
          <span className={styles.recvCode}>
            {recvCode}
          </span>
          <CopyToClipboard text={recvCode} onCopy={() => Toast.success('')}>
            <span className={styles.btnCopy}>
              
            </span>
          </CopyToClipboard>
        </div>
        <div>
          3. 
        </div>
        <div className={styles.qrcodeContainer}>
          <QRCode value={recvLink} />
        </div>

        <div className={styles.connectTips}>
          <br />
          10 <br />
          
        </div>
        <Button type="primary" className={styles.btnWaitConnect} disabled>
          ...
        </Button>
      </>
    );
  }

  renderStep3() {
    const {
      curFileId,
      files,
      peerState,
    } = this.props;

    const totalBytes = files.reduce((sum, cur) => {
      return sum + cur.size;
    }, 0);

    let allCompleted = true;
    files.forEach(f => {
      if (f.pct < 100) {
        allCompleted = false;
      }
    });

    let btnContent;
    if (allCompleted) {
      btnContent = '';
    } else if (peerState === 'connecting') {
      btnContent = '...';
    } else if (peerState === 'connected') {
      btnContent = '';
    } else if (peerState === 'transfer') {
      btnContent = `...(${prettyBytes(this.bps || 0)}/s)`;
    } else if (peerState === 'disconnected' || peerState === 'connectFailed') {
      btnContent = '...';
    }

    let btnDisabled = true;
    if (allCompleted) {
      btnDisabled = false;
    }

    return (
      <>
        <div className={styles.sendingBox}>
          <FileBox files={files} curFileId={curFileId} />
        </div>
        <div className={styles.sendingSummary}>
          <div>{files.length}{prettyBytes(totalBytes)}</div>
        </div>
        <Button type="primary" className={styles.btnSending} disabled={btnDisabled} onClick={this.onReset}>
          {btnContent}
        </Button>
      </>
    );
  }

  render() {
    const {
      curStep,
      files,
    } = this.props;

    return (
      <div className={styles.base}>
        <div className={styles.titleRow}>
          {((curStep === 1 && files.length > 0) || (curStep === 3)) && (
            <div className={styles.back} onClick={this.onReset}>
              
            </div>
          )}
          {curStep === 2 && (
            <div className={styles.back} onClick={this.onClickBack}>
              
            </div>
          )}
          <div className={styles.title}>
            
          </div>
        </div>
        <Steps>
          <Step
            index={1}
            title=""
            active={curStep === 1}
          />
          <Step index={2} title="" active={curStep === 2} />
          <Step index={3} title="" active={curStep === 3} />
        </Steps>
        {curStep === 1 && this.renderStep1()}
        {curStep === 2 && this.renderStep2()}
        {curStep === 3 && this.renderStep3()}
      </div>
    );
  }
}

SendFilePanel.propTypes = {
  curStep: PropTypes.number,
  files: PropTypes.array,
  curFileId: PropTypes.string,
  peerState: PropTypes.string,
  recvCode: PropTypes.string,
  setState: PropTypes.func,
};

export default SendFilePanel;
