import React, { Component } from 'react';
import axios from 'axios';
import Input from './common/Input';
import Button from './common/Button';
import Toast from './common/Toast';

import styles from './ContactPanel.cm.styl';

class ContactPanel extends Component {
  constructor(props) {
    super(props);
    this.state = {
      contact: '',
      content: '',
    };

    this.handleContactChange = this.handleContactChange.bind(this);
    this.handleContentChange = this.handleContentChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
    document.title = 'P2P - ';
  }

  handleContactChange(value) {
    this.setState({
      contact: value,
    });
  }

  handleContentChange(e) {
    this.setState({
      content: e.target.value,
    });
  }

  handleSubmit() {
    const {
      contact,
      content,
    } = this.state;

    if (!contact) {
      Toast.error('');
      return;
    }

    if (!content) {
      Toast.error('');
      return;
    }

    axios.post('/api/feedback', {
      contact,
      content,
    })
    .then((res) => {
      Toast.success('');
      this.setState({
        contact: '',
        content: '',
      });
    });
  }

  render() {
    const {
      contact,
      content,
    } = this.state;

    return (
      <div className={styles.base}>
        <div className={styles.title}>
          
        </div>
        <Input placeholder="" inputClassName={styles.input} value={contact} onChange={this.handleContactChange} />
        <textarea placeholder="" rows="6" className={styles.textarea} value={content} onChange={this.handleContentChange} />
        <Button type="primary" className={styles.btn} onClick={this.handleSubmit}>
          
        </Button>
        <div className={styles.tips}>
          <br />
           <a target="_blank" rel="noopener noreferrer" href="mailto:support@deershare.com">support@deershare.com</a>
        </div>
      </div>
    );
  }
}

export default ContactPanel;
