import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import {
  withRouter,
  matchPath,
} from 'react-router';

import {
  Link,
} from 'react-router-dom';

import styles from './NavBar.cm.styl';

function NavBar(props) {
  const pathname = props.location.pathname;

  const matchRecv = matchPath(pathname, '/recv/:recvCode?');

  return (
    <div className={styles.base}>
      <a href="/">
        <img src="/images/logo.png" alt="logo" className={styles.logo} />
      </a>
      <div className={styles.slogan}>
        P2P
      </div>
      <div className={styles.menus}>
        <Link to="/send">
          <span
            className={classnames(styles.menu, pathname === '/send' && styles.active)}
          >
            
          </span>
        </Link>
        <Link to="/recv">
          <span
            className={classnames(styles.menu, matchRecv && styles.active)}
          >
            
          </span>
        </Link>
        <Link to="/contact">
          <span className={classnames(styles.menu, pathname === '/contact' && styles.active)}>
           
          </span>
        </Link>
      </div>
    </div>
  );
}

NavBar.defaultProps = {};

NavBar.propTypes = {
  location: PropTypes.object,
};

export default withRouter(NavBar);
